import React,{ Component} from 'react';
import { connect } from 'react-redux'
import Index from '../components/home/Index';
import {fetchComingSoonLists} from '../actions'

class App extends Component {
  constructor(props) {
    super(props)
  }
  componentDidMount() {
    const { dispatch } = this.props
    dispatch(fetchComingSoonLists(1,3))
  }
  render() {
    return <Index/>
  }

}
App = connect()(App)
export default App;



