// import fetch from 'isomorphic-fetch'
import 'whatwg-fetch';
import * as types from '../constants/ActionTypes'


/**
 * get
 * @param  {String} options.url   api
 * @param  {String} options.query query
 * @return {Promise}               Promise
 */
const _get = (dispatch,{ url, query }) => {
  // dispatch({type:types.START_LOADING})
  let _url;
  if (query) {
    _url = `http://m.maizuo.com/v4/api${url}?${query}`;
  } else {
    _url = `http://m.maizuo.com/v4/api${url}`;
  }
  _url='https://www.reddit.com/r/reactjs.json'
  return fetch(_url)
    .then(res => {
      console.log(res.json())
      dispatch({type:types.FINISH_LOADING})
      if (res.status >= 200 && res.status < 300) {
        return res.data;
      }
      // return Promise.reject(new Error(res.status));
    });
};

/**
 * 
 * @param  {Function} options.dispatch store
 * @param  {Number} page             
 * @param  {Number} count             
 * @return {Promise}                  Promise
 */
export const fetchComingSoonLists = (page,count) => {
  const url = '/film/coming-soon';
  const query = `count=${count}&page=${page}&_t=`+new Date().getTime();
  return (dispatch) =>{
    _get(dispatch,{ url, query})
      .then((json) => {
        if (json.status===0) {
          return dispatch({type:types.FETCH_COMING_SOON_SUCCESS, films:json.data});
        }
        return Promise.reject(new Error('fetchFilmsLists failure'));
      })
      .catch((error) => {
        dispatch({type:types.FETCH_COMING_SOON_FAIL});
        return Promise.reject(error);
      });
  }

};



